import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom'; // 뒤로가기를 활성화 하기 위한 Hook 기능
import styled from 'styled-components';
// import Alert from './Alert.css';

const Box = styled.div`
  padding-top: 30px;
`;
const Title = styled.h4`
  font-size: 25px;
  color: ${props => props.color};
`;
// className을 사용하지 않아서 컴포넌트가 수십개가 되면, 혹시나 하는 실수에 대비하기 위해 Styled-components를 사용한다.

function Detail(props) {

  const [alert, setAlert] = useState(true);
  const [inputData, setInputData] = useState(''); //state의 위치는 useEffect 위에 두는 것이 실행이 되는 방법이다.
  console.log(inputData);

  useEffect(() => {
    const Timer = setTimeout(() => { setAlert(false) }, 2000);
    // setTimeout이라는 명령어를 통해서 useState 값을 false로 변경하면서 2초뒤에 사라지는 타이머 변수를 useEffect로 만들어준다.
    console.log("alert 확인창")
    return () => { clearTimeout(Timer) } // Detail컴포넌트를 2초 안에 나올 경우 setTimeout 실행을 할 때 발생하는 에러를 없애준다.
  }, [alert]); // useEffect에 조건문을 사용하기 위해선 []또는 []안에 state를 넣어준다.

  const history = useHistory(); // 변수를 선언해서 useHistory를 담아줘야 한다.
  const { id } = useParams();

  const FindItem = props.shoesdata.find(function (Item) {
    return Item.id === id
  })

  return (
    <div className="container">

      <Box>
        <Title className="red">
          Detail
        </Title>
      </Box>

      <input onChange={(e) => {
        setInputData(e.target.value)
      }} />

      {
        alert === true
          ? (<div className="alert">재고가 얼마 남지 않았습니다.</div>)
          : null //삼항연산자를 통해서 alert가 true인경우 실행할 방법과 false인 경우 실행할 방법을 구분해준다.
      }

      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" alt='' />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{FindItem.title}</h4>
          <p>{FindItem.content}</p>
          <p>{FindItem.price}원</p>

          <button className="btn btn-danger">주문하기</button>
          <button className="btn btn-danger" onClick={() => {
            history.push('/'); // onClick 이벤트를 통해 뒤로가기를 활성화 하자
          }}>뒤로가기</button>
        </div>
      </div>
    </div>
  )
}

// function StockMenu(props) {
