import React, { useState } from 'react';
import './App.css';
import Data from './data.js'
import { Link, Route, Switch } from 'react-router-dom';
import Detail from './Detail';
import axios from 'axios';


function App() {
  const [shoesdata, setShoesData] = useState(Data);
  console.log(setShoesData);

  return (
    <div className="App">
      <nav class="navbar navbar-expand-lg navbar-light bg-light">

        <div class="container-fluid">

          <a class="navbar-brand" href="#!">Navbar</a>
          <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#!navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>

          <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav me-auto mb-2 mb-lg-0">

              <li class="nav-item">
                <a class="nav-link active" aria-current="page" href="#!">
                  <Link to='/'>Home</Link>
                </a>
              </li>

              <li class="nav-item">
                <a class="nav-link" href="#!">
                  <Link to='/detail'>Detail</Link>
                </a>
              </li>

              <li class="nav-item dropdown">
                <a class="nav-link dropdown-toggle" href="#!" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  Dropdown
                </a>
                <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li><a class="dropdown-item" href="#!">Action</a></li>
                  <li><a class="dropdown-item" href="#!">Another action</a></li>
                  <li><hr class="dropdown-divider" /></li>
                  <li><a class="dropdown-item" href="#!">Something else here</a></li>
                </ul>
              </li>

              <li class="nav-item">
                <a class="nav-link disabled" href="#!">Disabled</a>
              </li>

            </ul>

            <form class="d-flex">
              <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
              <button class="btn btn-outline-success" type="submit">Search</button>
            </form>

          </div>
        </div>
      </nav>

      <Switch>
        <Route exact path='/'>
          <div className="background">
            <h1>20% Season OFF</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style
              component for calling extra attention to featured content or information.
            </p>
            <p>
              <button variant="primary">Learn more</button>
            </p>
          </div>
          <div className="container">
            <div className="row">
              {
                shoesdata.map((a, i) => {
                  return <Card shoesdata={a} i={i} key={i} />
                })
              }
            </div>
            <button className='btn btn-primary' onClick={() => {
              axios.get('https://codingapple1.github.io/shop/data2.json')
                .then((result) => {
                  console.log(result.data)
                  setShoesData([...shoesdata, ...result.data])
                })
                .catch(() => {
                  console.log('실패했어요')
                })
            }}>더보기</button>
          </div>
        </Route>
        <Route path='/detail/:id'>
          <Detail shoesdata={shoesdata} />
        </Route>
      </Switch>

    </div >
  );
}

function Card(props) {
  return (
    <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes' + (props.i + 1) + '.jpg'} alt='' width='100%' />
      <h4>{props.shoesdata.title}</h4>
      <p>{props.shoesdata.content}</p>
      <p>{props.shoesdata.price}</p>
    </div>
  )
}

export default App;
